
import { defineComponent } from 'vue';
import supabase from '../components/client.vue';
 // @ is an alias to /src

supabase.from('users').on('UPDATE',res => {
  console.log(res.commit_timestamp)
})
export default defineComponent({
  name: 'App',

  data () {
    return {
      //
      email:'',
      name: ''
    }
  }, methods: {
     async login() {
      await supabase.auth.signIn({ email:this.email })
       try {
        supabase.from("users").insert({
          email: this.email,
          name: this.name
        })

      } catch (e) {
        throw new Error(`${e}`)
      }
    }
  }
})

